#profileImg{
    width: 60%;
    height: auto;
    border-radius: 20px;
    box-sizing: border-box;
    border: 1px solid gray;
    transition: all 1s ease;
}

#profileImg:hover{
    /* box-shadow: 0 0 15px 2px black; */
    filter: drop-shadow(0 0 4px black);
}

#name{
    color: green;
}

#resume{
    background-color: #111111;
    color: white;
    text-decoration: none;
    border: 1px solid #111111;
    padding: 7px 20px;
    border-radius: 5px;
    font-weight: 500;
}

#resume:hover{
    background-color: white;
    color: black;
    transition: all 1s ease;
}

.fa-download{
    margin-left: 10px;
}

.fa-brands{
    font-size: 35px;
    margin: .2rem;
    color: #111111;
}

.fa-brands:hover{
    color: crimson;
}

/* Loader home */
#loader{
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}